import * as React from 'react';
import { TouchableWithoutFeedback, View } from 'react-native';

import { Theme, withTheme } from '../../theme';
import { getOverlayStyles } from './Overlay.styles';

interface OverlayProps {
  onPress: () => void;
  theme: Theme;
  transparent?: boolean;
}

const OverlayBase = (props: OverlayProps) => {
  const { onPress, transparent = false, theme } = props;

  const { overlayStyle } = getOverlayStyles({ transparent }, theme);

  return (
    <TouchableWithoutFeedback
      onPress={event => {
        // @ts-ignore
        event.preventDefault();
        onPress();
      }}
    >
      <View style={overlayStyle} />
    </TouchableWithoutFeedback>
  );
};

export const Overlay = withTheme(OverlayBase);
try {
    // @ts-ignore
    Overlay.displayName = "Overlay";
    // @ts-ignore
    Overlay.__docgenInfo = { "description": "", "displayName": "Overlay", "props": { "onPress": { "defaultValue": null, "description": "", "name": "onPress", "required": true, "type": { "name": "() => void" } }, "transparent": { "defaultValue": null, "description": "", "name": "transparent", "required": false, "type": { "name": "boolean" } } } };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["src/components/Overlay/Overlay.tsx#Overlay"] = { docgenInfo: Overlay.__docgenInfo, name: "Overlay", path: "src/components/Overlay/Overlay.tsx#Overlay" };
}
catch (__react_docgen_typescript_loader_error) { }